import React, { PropTypes } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import BaseComponent from './BaseComponent';
import Style from './Complete.cssmodule.less';
import {
    clickButton,
} from '../../actions/Modal';
import Button from '../CommonSet/Button';
import Title from '../CommonSet/Title';
import Media from '../CommonSet/Media';

class Complete extends BaseComponent {
    constructor(props) {
        super(props);
        this.handleButtonClick = this.handleButtonClick.bind(this);
        const { isMazeMode = false, isLastStudy = false } = entrylms.getData();
        this.isMazeMode = isMazeMode;
        this.isLastStudy = isLastStudy;
    }

    handleButtonClick(event) {
        const { clickButton } = this.props.actions;
        clickButton({
            event
        });
    }

    getTitleName() {
        let option = '';

        if (this.props.item.titleType) {
            option = ` ${this.props.item.titleType}`;
        }

        return `${Style.title} ${option}`;
    }

    createMediaView() {
        let view = null;

        if (this.props.item.media) {
            view = (
                <Media
                    className={Style.media}
                    media={this.props.item.media}
                    option={{
                        width: 308,
                        height: 190
                    }}
                />
            );
        }

        return view;
    }

    createCustomButton() {
        const { buttons = [] } = this.props.item;

        const customButtons = buttons.map((button, i)=> {
            return (
                <Button
                    key={`customButton${i}`}
                    className={Style.button}
                    text={button.text}
                    onClick={(e) => button.click(e)}
                />
            )
        });

        return (
            <div>
                { customButtons }
            </div>
        )
    }

    render() {
        const { isHomework, isHomeworkLapse, isSubmit, isLastStudy, media = {} } = this.props.item;
        return (
            <div
                className={`${Style.complete} ${media.category || ''}`}
            >
                <Title
                    className={this.getTitleName()}
                    isClose={false}
                >
                    { this.props.item.title }
                </Title>
                <div
                    className={Style.contentView}
                >
                    <div
                        className={Style.content}
                    >
                        { this.createMediaView() }
                        <div
                            dangerouslySetInnerHTML={{
                                __html: this.props.item.content
                            }}
                        />
                    </div>
                    { (isHomework && !isHomeworkLapse && (isSubmit || (this.isMazeMode && this.isLastStudy))) && (
                        <Button
                            className={Style.button}
                            text={this.Utils.getLang('Buttons.course_submit')}
                            onClick={() => this.handleButtonClick('submit')}
                        />
                    )}
                    { (this.props.item.titleType !== 'FAIL' && (!this.isMazeMode || (this.isMazeMode && (!isLastStudy || !isHomework)))) && (
                        <Button
                            className={Style.button}
                            text={(!isLastStudy) ? this.Utils.getLang('Buttons.next_lesson') : this.Utils.getLang('Menus.course_done_study')}
                            onClick={() => this.handleButtonClick('next')}
                        />
                    )}
                    { this.createCustomButton() }
                    <Button
                        className={Style.prevButton}
                        onClick={() => this.handleButtonClick('prev')}
                    >
                        <div
                            className={Style.text}
                        >
                            {this.Utils.getLang('Buttons.previous')} 

                        </div>
                    </Button>
                </div>
            </div>
        );
    }
}

Complete.displayName = 'Complete';
Complete.propTypes = {};

function mapDispatchToProps(dispatch) {
    const actions = { clickButton };
    const actionMap = { actions: bindActionCreators(actions, dispatch) };
    return actionMap;
}

export default connect(undefined, mapDispatchToProps)(Complete);



