import React, { Component, PropTypes } from 'react';
import { compStatusTypes } from '../../Constants';

class Button extends Component {
    constructor(props) {
        super(props);
        this.handleOnClick = this.handleOnClick.bind(this);
    }

    getButtonClassName() {
        return `button ${this.props.className}`;
    }

    handleOnClick(e) {
        if (this.props.status !== compStatusTypes.DISABLED
            && this.props.status !== compStatusTypes.HIDDEN) {
            this.props.onClick(e);
        }
    }

    createButtonContent() {
        let content = null;
        if (this.props.children) {
            content = this.props.children;
        } else if (this.props.text) {
            content = this.props.text;
        }
        return content;
    }

    render() {
        return (
            <div
                className={this.getButtonClassName()}
                onClick={this.handleOnClick}
            >
                { this.createButtonContent() }
            </div>
        );
    }
}


Button.displayName = 'Button';
Button.propTypes = {
    children: PropTypes.object,
    className: PropTypes.string,
    status: PropTypes.oneOf(Object.keys(compStatusTypes)),
    onClick: PropTypes.func,
    text: PropTypes.string,
};
Button.defaultProps = {
    className: '',
    status: compStatusTypes.ENABLED,
    onClick() {},
    text: '',
};

export default Button;



