import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import BaseComponent from './BaseComponent';
import Project from './Project';
import Intro from './Intro';
import Alert from './Alert';
import Confirm from './Confirm';
import Maze from './Maze';
import Diary from './Diary';
import MazeResult from './MazeResult';
import Follow from './Follow';
import Complete from './Complete';
import Manual from './Manual';
import LectureProgress from './LectureProgress';
import Element from './Element';
import {
    clickButton,
} from '../../actions/Modal';
import { 
    compStatusTypes, 
    ModalTypes,
    MediaStatusTypes,
} from '../../Constants';
import Style from './ModalView.cssmodule.less';

class ModalView extends BaseComponent {
    createView() {

        if (this.props.status === compStatusTypes.HIDDEN) {
            return null;
        }

        let child = null;
        const { items = [], itemIndex = 0 } = this.props;
        const maxIndex = items.length;
        const item = (itemIndex <= maxIndex - 1) ? items[itemIndex] : items[maxIndex - 1];
        const { className } = item;
        switch (item.type) {
        case ModalTypes.HINT :
        case ModalTypes.PREBUILT_HINT :
        case ModalTypes.OUTRO :
        case ModalTypes.INTRO :
            const { media = {} } = item;
            const { category } = media;

            if(maxIndex === 1 && (category === MediaStatusTypes.PROJECT || category === MediaStatusTypes.VIDEO)) {
                child = (
                    <Project 
                        item={item}
                    />
                );
            } else {
                child = (
                    <Intro
                        type={item.type}
                        item={item}
                        itemIndex={itemIndex}
                        maxIndex={maxIndex}
                    />
                );
            }

            break;
        case ModalTypes.ALERT :
            child = (
                <Alert
                    item={item}
                    theme={item.theme}
                />
            );
            break;
        case ModalTypes.CONFIRM :
            child = (
                <Confirm
                    item={item}
                    theme={item.theme}
                />
            );
            break;
        case ModalTypes.MAZE :
            child = (
                <Maze
                    item={item}
                    theme={item.theme}
                    itemIndex={itemIndex}
                    maxIndex={maxIndex}
                />
            );
            break;
        case ModalTypes.DIARY :
            child = (
                <Diary
                    item={item}
                    theme={item.theme}
                />
            );
            break;
        case ModalTypes.MAZE_RESULT :
            child = (
                <MazeResult
                    item={item}
                    theme={item.theme}
                />
            );
            break;
        case ModalTypes.FOLLOW :
            child = (
                <Follow
                    item={item}
                />
            );
            break;
        case ModalTypes.COMPLETE :
            child = (
                <Complete
                    item={item}
                />
            );
            break;
        case ModalTypes.LECTURE_PROGRESS :
            child = (
                <LectureProgress
                    item={item}
                />
            );
            break;
        case ModalTypes.MANUAL :
            child = (
                <Manual
                    item={item}
                    itemIndex={itemIndex}
                />
            );
            break;
        case ModalTypes.ELEMENT :
            child = (
                <Element
                    item={item}
                />
            );
            break;
        default :
            child = null;
            break;
        }

        return (
            <div
                className={`${Style.modalViewChild} ${Style[className]}`}
                onClick={(e)=> {
                    e.stopPropagation();
                }}
            >
                { child }
            </div>
        );
    }

    render() {
        const { option = {} } = this.props;
        const { isCloseOutsideClick } = option;
        return (
            <div
                className={`${Style.modalView} ${this.props.status}`}
                onClick={(e)=> {
                    if(isCloseOutsideClick) {
                        const { clickButton } = this.props.actions;
                        clickButton({
                            event: 'outside',
                        });
                    }
                }}
            >
                { this.createView() }
            </div>
        );
    }
}

ModalView.displayName = 'ModalView';

function mapStateToProps(state) {
    const props = {
        itemIndex: state.Modal.itemIndex,
        status: state.Modal.status,
    };
    return props;
}

function mapDispatchToProps(dispatch) {
    const actions = { clickButton };
    const actionMap = { actions: bindActionCreators(actions, dispatch) };
    return actionMap;
}

export default connect(mapStateToProps, mapDispatchToProps)(ModalView);



