import React, { PropTypes } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import BaseComponent from './BaseComponent';
import Style from './Project.cssmodule.less';
import {
    ModalTypes,
} from '../../Constants';
import {
    clickButton,
} from '../../actions/Modal';
import Button from '../CommonSet/Button';
import Title from '../CommonSet/Title';
import Media from '../CommonSet/Media';

class Project extends BaseComponent {
    constructor(props) {
        super(props);

        this.handleButtonClick = this.handleButtonClick.bind(this);
    }

    handleButtonClick(event) {
        const { clickButton } = this.props.actions;
        clickButton({
            event
        });
    }

    getTitleName() {
        let option = '';

        if (this.props.item.titleType) {
            option = ` ${this.props.item.titleType}`;
        }

        return `${Style.title} ${option}`;
    }

    createHtmlContent(contnet) {
        let contentHTML = '';

        if(contnet) {
            contentHTML = contnet.replace(/\*\*\*.*?\*\*\*/gim, (item)=> {
                let block = item.substring(3, item.lastIndexOf('***'));
                return $('<div>').append(this.createRenderView(block)).html();
            });
        }

        return contentHTML;
    }

    createContent() {
        let view = null;

        if (this.props.item.content) {
            view = (
                <div
                    className={Style.content}
                    dangerouslySetInnerHTML={{
                        __html: this.createHtmlContent(this.props.item.content),
                    }}
                />
            );
        }

        return view;
    }

    createMediaView() {
        let view = null;

        if (this.props.item.media) {
            view = (
                <Media
                    className={Style.media}
                    media={this.props.item.media}
                    checkScript={this.props.item.checkScript}
                    option={{
                        width: '580',
                        height: '327',
                    }}
                />
            );
        }

        return view;
    }

    createButtonView() {
        const { buttons = [], type = ModalTypes.INTRO } = this.props.item;

        let buttonText = '';

        if(type === ModalTypes.INTRO) {
            buttonText = this.Utils.getLang('Buttons.get_started');
        } else if(type === ModalTypes.HINT || type === ModalTypes.PREBUILT_HINT){
            buttonText = this.Utils.getLang('Buttons.confirm');
        }

        return (
            <div
                className={Style.buttonView}
            >
                <Button
                    className={Style.button}
                    onClick={e => this.handleButtonClick('start')}
                    text={buttonText}
                />
            </div>
        )
    }

    getTitleBadge() {
        const lmsData = entrylms.getData() || {};
        const { type } = lmsData;
        let badgeText = '';
        switch(type) {
            case 'guide': 
                badgeText = '따라하기';
                break;
            case 'mission': 
                badgeText = '도전하기';
                break;
            case 'free': 
            default :
                badgeText = '자유롭게 만들기';
                break;
        }

        return badgeText;
    }

    render() {
        const { isHomework, isHomeworkLapse, isSubmit, isLastStudy, media = {} } = this.props.item;
        return (
            <div
                className={`${Style.project} ${media.category || ''}`}
            >
                <Title
                    className={this.getTitleName()}
                    onClose={e => this.handleButtonClick('close')}
                >   
                    <div
                        className={`${Style.badge}`}
                    >
                        { this.getTitleBadge() }
                    </div>
                    <div
                        className={`${Style.titleText}`}
                    >
                        { this.props.item.title }
                    </div>
                </Title>
                <div
                    className={Style.contentView}
                >
                    { this.createContent() }
                    { this.createMediaView() }
                </div>
                { this.createButtonView() }
            </div>
        );
    }
}

Project.displayName = 'Project';
Project.propTypes = {};

function mapDispatchToProps(dispatch) {
    const actions = { clickButton };
    const actionMap = { actions: bindActionCreators(actions, dispatch) };
    return actionMap;
}

export default connect(undefined, mapDispatchToProps)(Project);



