import React, { Component } from 'react';
import Utils from '../../sources/Utils';
import Styles from './BaseComponent.cssmodule.less';
import {
    ModalTypes,
    ThemeTypes,
} from '../../Constants';
import AlertStyle from '../../themes/BlueTheme/Alert.cssmodule.less';
import ConfirmStyle from '../../themes/BlueTheme/Confirm.cssmodule.less';
import CP2017S2MazeStyle from '../../themes/CP2017S2/Maze.cssmodule.less';
import CP2017S2DiaryStyle from '../../themes/CP2017S2/Diary.cssmodule.less';
import CP2017S2MazeResultStyle from '../../themes/CP2017S2/MazeResult.cssmodule.less';

class BaseComponent extends Component {
    constructor(props) {
        super(props);
        this.Styles = Styles;
        this.Utils = Utils;

        this.themes = {
            [ThemeTypes.BLUE] : {
                [ModalTypes.ALERT] : AlertStyle,
                [ModalTypes.CONFIRM] : ConfirmStyle,
            },
            [ThemeTypes.CP2017S2] :  {
                [ModalTypes.MAZE] : CP2017S2MazeStyle,
                [ModalTypes.DIARY] : CP2017S2DiaryStyle,
                [ModalTypes.MAZE_RESULT] : CP2017S2MazeResultStyle,
            }
        }
    }

    getTheme(type, name) {
        const themeList = this.themes[type] || {};
        return themeList[name];
    }

    render() {
        return (
            <div>
                BaseComponent
            </div>
        );
    }
}

export default BaseComponent;



