import modal from './components/Modal';
import { EventEmitter } from 'fbemitter';
import {
    ModalTypes,
    ThemeTypes,
} from './Constants';

const { General = {} } = Lang;
const { alert_title = '알림', confirm_title = '확인' } = General;
export const Modal = modal;
const alertModal = new modal(
    { title: alert_title, type: ModalTypes.ALERT, theme: ThemeTypes.BLUE },
    { isCommon: true }
);
//function _alert(content){
function _alert(content, title, options) {
    alertModal.modalData = Object.assign({}, alertModal.modalData[0], {
        content,
        options,
        title: title || alert_title,
    });
    alertModal.show();
    alertModal.on('hide', () => {
        alertModal.off('hide');
    })
    return alertModal;
}
export const alert = _alert;

const confirmModal = new modal(
    { title: confirm_title, type: ModalTypes.CONFIRM, theme: ThemeTypes.BLUE },
    { isCommon: true }
);
function _confirm(content, title, options) {
    const { Menus = {} } = Lang;
    const { popup_notice = "알림" } = Menus;
    confirmModal.modalData = Object.assign({}, confirmModal.modalData[0], {
        content,
        options,
        title: title || confirm_title,
    });
    confirmModal.show();
    return new Promise((resolve, reject) => {
        confirmModal.on('click', function (event) {
            let isConfirm = event === 'ok';
            resolve(isConfirm);
        });
        confirmModal.on('hide', function () {
            resolve(false);
        });
    });
}
export const confirm = _confirm;



