import React, { PropTypes } from 'react';
import BaseComponent from './BaseComponent';
import Button from './Button';

class ProjectView extends BaseComponent {
    constructor(props) {
        super(props);

        this.handleLoad = this.handleLoad.bind(this);
    }

    handleLoad() {
        const { checkScript } = this.props;
        const { contentWindow = {} } = this.projectView;
        const { Entry = {} } = contentWindow;

        if(checkScript) {
            Entry.container.addExtension(
                new Entry.TargetChecker(checkScript, false, entrylms.getData().type + "_intro"));
            Entry.targetChecker.generateStatusView(true);
        }
        contentWindow.document.body.className = "";
    }

    render() {

        return (
            <iframe
                src={ `/api/iframe/project/${ this.props.content }?play=true&hideplaybtn=true` }
                ref={dom=> {
                    this.projectView = dom;
                }}
                onLoad={this.handleLoad}
            />
        );
    }
}

ProjectView.displayName = 'ProjectView';
ProjectView.propTypes = {
};

export default ProjectView;



